        width: 133px;
    }


    /* Бесплатная консультация */

    .consultation {
        margin-top: 28px;
    }

    .consultation__form {
        padding: 13px 0 20px 0;
    }

    .consultation__form>h2 {
        font-size: 25.5px;
    }

    .consultation__form-solovey>input {
        border-radius: 9px;
        width: 100%;
        height: 37px;
        font-weight: 600;
        font-size: 12px;
        padding-left: 20px;
    }

    .consultation__form-solovey {
        gap: 10px;
    }

    .consultation__form-solovey>button {
        width: 100%;
        height: 37px;
        font-size: 20px;
        margin-top: 5px;
    }

    .privacy {
        font-size: 8px;
        padding: 8px 0 0 0;
    }

    .privacy-line {
        text-underline-offset: 1.3px;
    }

    .demand-specialists {
        display: none;
    }

    section.about_courses {
        display: none;
    }

    .courses__header1 {
        display: none;
    }

    /* Секция об связи с колледжем */

    .enrollment {
        background: #e44a77;
        padding: 12px 0 52px;
        position: relative;
        overflow: hidden;
    }

    .enrollment__wrapper {
        padding: 0;
    }

    .enrollment__head-title {
        font-weight: 800;
        font-size: 30px;
        margin-bottom: 4px;
        line-height: 37.50px;
    }

    .enrollment__head-text {
        font-weight: 400;
        font-size: 15px;
        line-height: 18.50px;
        max-width: 100%;
    }

    .enrollment__form form {
        display: flex;
        width: 100%;
        flex-direction: column;
        gap: 10px;
    }

    .enrollment__form {
        margin-top: 18px;
    }

    .form__input,
    .form__input2,
    .form__input3,
    .form__button {
        font-weight: 400;
        font-size: 18px;
        width: 100%;
        margin: 0;
    }

    .enrollment__form form button:not(.visible-mobile) {
        display: none;
    }

    .enrollment__form form button.visible-mobile {
        font-weight: 800;
        margin-top: 7px;
    }

    .enrollment__rock {
        width: 113px;
        height: 112px;
        left: 0;
        top: -30px;
    }


    /* Секция о колледже в КЗ */

    .about_hexlet {
        padding: 18px 0 28px;
    }

    .about_hexlet__head-text>* {
        width: 100%;
        padding: 0;
    }

    .about_hexlet__head-title {
        font-size: 30px;
        padding: 0;
        line-height: 37.50px;
        margin-bottom: 16px;
    }

    .about_hexlet__head-details {
        font-weight: 400;
        font-size: 15px;
        line-height: 18.50px;
        padding: 0;
        margin-bottom: 16px;
    }

    .about_hexlet__head-img,
    .about_hexlet__head-img img {
        margin: 0;
        width: 100%;
        max-width: 100%;
        height: 276px
    }

    .about_hexlet__head {
        margin-bottom: 36px;
        display: block;
    }

    .about_hexlet__advantages {
        padding: 0;
        flex-direction: column;
        gap: 32px;
    }

    .about_hexlet__advantages-titles {
        font-weight: 700;
        font-size: 24px;
        line-height: 30px;
        margin-top: 0;
        margin-bottom: 10px;
    }

    .about_hexlet__advantages-soft {
        display: block;
    }

    .about_hexlet__advantages-relevance,
    .about_hexlet__advantages-soft,
    .about_hexlet__advantages-flexibility {
        max-width: 100%;
        width: 100%;
    }

    .about_hexlet__advantages-relevance>.about_hexlet__advantages-texts,
    .about_hexlet__advantages-soft>.about_hexlet__advantages-texts,
    .about_hexlet__advantages-flexibility>.about_hexlet__advantages-texts {
        min-height: max-content;
        max-width: 100%;
        font-weight: 400;
        font-size: 15px;
        line-height: 18.50px;
    }


    /* Секция Transition */

    .transition {
        padding: 20px 0 40px;
    }

    .transition__wrapper {
        display: block;
    }

    .transition__content-title {
        padding: 0;
        font-weight: 800;
        font-size: 30px;
        margin-bottom: 4px;
    }

    .transition__content-text {
        padding: 0;
        font-weight: 400;
        font-size: 15px;
        line-height: 18.50px;
        width: 100%;
        margin-bottom: 20px;
    }

    .transition__btn {
        margin: 0;
        width: 100%;
        height: 50px;
        font-weight: 800;
        font-size: 20px;
    }


    /* Багнутые элементы */

    .footer__sections>* {
        width: 90%;
    }

    .details__teachers-about,
    .details__projects-about,
    .details__grafic-about,
    .demand-specialists__description {
        width: 100%;
    }

    .footer__section-contacts,
    .copyright__main,
    .footer__copyright,
    .footer__copyright-political,
    .footer {
        display: none;
    }

}

